      if (disc.offsetWidth == 30) {
        let music1 = document.querySelector(".music1")
        disc.classList.toggle("big")
        disc.classList.add("yuanjiao")
        disc.classList.remove("yinyue1")
        music1.classList.toggle("big1")
        // console.log(yinyue)
      }
    },
    //ÂºÄÂßãÊí≠ÊîæÂêéÁöÑÂõûË∞ÉÂáΩÊï∞
    onPlay() {
      let fff = document.querySelector("#vd1").firstElementChild
      console.log(fff)
      console.log("ÂºÄÂßãÊí≠Êîæ"+fff.src)
      this.flag = true
    },
    //ÊöÇÂÅúÊí≠ÊîæÁöÑÂõûË∞ÉÂáΩÊï∞
    onPause() {
      console.log("ÊöÇÂÅúÊí≠Êîæ")
      this.flag = false
    },
    //Êí≠ÊîæÂÆåÊØï‰∏ã‰∏ÄÈ¶ñ
    overAudio() {
      console.log("ÁªìÊùüÊí≠Êîæ")
      let index = this.music_data.indexOf(this.my_src)
      console.log("index: " + index)
      index = index + 1
      if (index >= this.music_data.length) {
        index = 0
      }
      this.my_src = this.music_data[index]
      let audio1 = document.querySelector("#vd1");
      audio1.load();
      this.bofang_index(index)
    },
    //Êí≠ÊîæÊåâÈíÆ
    boFang() {
      let audio1 = document.querySelector("#vd1");
      if (audio1.paused) {
        audio1.play();
      } else {
        audio1.pause();
      }
    },
    //‰∏ä‰∏ÄÈ¶ñ
    topp() {
      console.log("‰∏ä‰∏ÄÈ¶ñ")
      let index = this.music_data.indexOf(this.my_src)
      index -= 1
      if (index < 0) {
        index = this.music_data.length - 1
      }
      this.my_src = this.music_data[index]
      let audio1 = document.querySelector("#vd1");
      audio1.load();
      this.bofang_index(index)
    },
    //‰∏ã‰∏ÄÈ¶ñ
    bott() {
      console.log("‰∏ã‰∏ÄÈ¶ñ")
      let index = this.music_data.indexOf(this.my_src)
      console.log("index: " + index)
      index = index + 1
      if (index >= this.music_data.length) {
        index = 0
      }
      this.my_src = this.music_data[index]
      let audio1 = document.querySelector("#vd1");
      audio1.load();
      this.bofang_index(index)
    },
