    },
    //Âà§Êñ≠Áà±ÂøÉÈ¢úËâ≤
    aixin(e) {
      if (this.aixin_ico) {
        e.target.style.color = "red"
        this.aixin_ico = false
      } else {
        e.target.style.color = "#515a6e"
        this.aixin_ico = true
      }
      
      if (e.target.style.color == "red") {
        this.modal_aixin = true
      } else {
        this.modal_aixin_1 = true
      }
    },
    //Âà§Êñ≠Êî∂ËóèÈ¢úËâ≤
    shouCang(e) {
