      }
      if (!this.shoucang_ico) {
        this.shou_cang = true
      }
    },
    //ÊâìÂºÄÊî∂Ëµ∑Èü≥‰πêÊ≠åÂçïÂàóË°®
    musciList() {
      let disc = this.$refs.bt_yinyue
      if (this.musciList_shenSuo) {
        disc.classList.remove("music_list_big")
